import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/pages/Home/index.vue'
import Search from '@/pages/Search'
import Login from '@/pages/Login'
import Register from '@/pages/Register'

// 使用VueRouter插件
Vue.use(VueRouter)

const VueRouterPush = VueRouter.prototype.push
// console.log(VueRouterPush)
VueRouter.prototype.push = function push(to, resolve, reject) {
    // call: 让一个函数成为指定任意对象的方法进行调用
    // VueRouterPush的this指向window，使用call将this指向vueRouter
    // 原型的this指向实例VueRouter的实例对象
    if (resolve && reject) {
        return VueRouterPush.call(this, to, resolve, reject)
    } else {
        return VueRouterPush.call(this, to, () => { }, () => { })
    }

}

export default new VueRouter({
    routes: [
        {
            path: '/',
            redirect: 'home'
        },
        {
            path: '/home',
            name: 'home',
            component: Home,
            meta: { show: true }
        },
        {
            path: '/search/:keyword?',
            name: 'search',
            component: Search,
            meta: { show: true },
            // 1
            // props: true
            // props: {
            //     a: 'xxxx',
            //     b: 'yyyy'
            // }
            props: route => {
                return { keyword: route.params.keyword }
            }
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/register',
            component: Register
        },
    ]
})