import { getBaseCategoryList, getBannerList } from "@/api/index"


const state = () => ({
    categoryList: [],
    bannerList: []
})

const mutations = {
    CATEGORYLIST(state, value) {
        state.categoryList = value
    },
    GETBANNERLIST(state, value) {
        state.bannerList = value
    },
}

const actions = {
    categoryList(context) {
        getBaseCategoryList().then(res => {
            if (res.code === 200) {
                context.commit('CATEGORYLIST', res.data)
            }
        })
    },
    getBannerList(context) {
        getBannerList().then(res => {
            if (res.code === 200) {
                console.log(res)
                context.commit('GETBANNERLIST', res.data)
            }
        })
    }
}

const getters = {}

export default {
    // namespaced: true,
    state,
    mutations,
    actions,
    getters
}