import { reqGetCode, reqUserRegister, reqUserLogin } from "@/api/index"

const state = () => ({
    code: '',
    token: localStorage.getItem('token') || ''
})

const mutations = {
    GETCODE(state, value) {
        state.code = value
    },
    USERLOGIN(state, value) {
        state.token = value
        localStorage.setItem('token', value)
    }

}

const actions = {
    async getCode(context, phone) {
        // return reqGetCode(phone).then(res => {
        //     if (res.code == 200) {
        //         console.log('action')
        //         context.commit('GETCODE', res.data)
        //         return res.data
        //     } else {
        //         return Promise.reject(new Error('fail'))
        //     }
        // })
        const res = await reqGetCode(phone)
        if (res.code == 200) {
            console.log('action')
            context.commit('GETCODE', res.data)
            return res.data
        } else {
            return Promise.reject(new Error('fail'))
        }
    },
    async userRegister({ commit }, data) {
        const res = await reqUserRegister(data)
        if (res.code == 200) {
            return 'ok'
        } else {
            alert(res.message)
            return Promise.reject(new Error('false'))
        }
    },
    async userLogin({ commit }, data) {
        const res = await reqUserLogin(data)
        if (res.code == 200) {
            commit('USERLOGIN', res.data.token)
            return 'ok'
        } else {
            alert(res.message)
            return Promise.reject(new Error('false'))
        }
    }
}

// getters为了简化数据存在
const getters = {

}

export default {
    // namespaced: true,
    state,
    mutations,
    actions,
    getters
}