// api接口统一管理
import request from "./request"
import mockRequest from "./mockAjax"

// 2.首页三级分类
export function getBaseCategoryList() {
    return request.get('/product/getBaseCategoryList')
}

// 获取banner
export function getBannerList() {
    return mockRequest.get('/banner')
}

//搜索商品  需至少给服务器传一个默认对象
export const reqGetSearchInfo = (params) => {
    // return request.post('/list', params)
    return request({ url: '/list', method: 'post', data: params })
}

// 获取验证码  /api/user/passport/sendCode/phone
export function reqGetCode(phone) {
    return request.get(`/user/passport/sendCode/${phone}`)
}

//注册
export const reqUserRegister = (params) => {
    // return request.post('/list', params)
    return request({ url: '/user/passport/register', method: 'post', data: params })
}

//登录
export const reqUserLogin = (params) => {
    // return request.post('/list', params)
    return request({ url: '/user/passport/login', method: 'post', data: params })
}